/** * Created by admin on 2017/5/2. */import React, { Component } from 'react';export default class Modal extends Component {	render() {		return(			<div style={styles.modal}>				<div style={styles.container}>					<p style={styles.btnContainer}>						<button style={styles.btn} onClick={this._onClick.bind(this)}>确认</button>						<button style={styles.btn} onClick={this._onClick.bind(this)}>取消</button>					</p>				</div>			</div>		)	}	_onClick() {		this.props._onClick && this.props._onClick();	}}const styles = {	modal: {		position: 'absolute',		left: 0,		top: 0,		bottom: 0,		right: 0,		background: 'rgba(0, 0 , 0, .4)'	},	container: {		background:'#fff',		width: '6rem',		height: '4rem',		position: 'absolute',		left: '50%',		top: '50%',		marginLeft: '-3rem',		marginTop: '-2rem',		//padding: '.rem'	},	btnContainer: {		display: 'flex',		justifyContent: 'space-around',		alignItems: 'center',		height: '1rem'	},	btn: {		width: '2rem',		padding: '.1rem',		background: '#fed423',		color: '#fff',		fontWeight: 'bold'	},};


